<template>
	<div>
		<div class="page-title">
			<h3>Счет</h3>

			<button class="btn waves-effect waves-light btn-small" @click="refresh">
				<i class="material-icons">refresh</i>
			</button>
		</div>

		<Loader v-if="loading"></Loader> <!-- вставка компонента, зарегистрированного глобально в main.js -->

		<div v-else class="row">
			<HomeBill
				v-bind:rates="currency.rates"></HomeBill>
			<HomeCurrency
				v-bind:currency="currency"></HomeCurrency>

			
		</div>
	</div>
</template>

<script>
import HomeBill from '@/components/HomeBill.vue'
import HomeCurrency from '@/components/HomeCurrency.vue'

export default {
  name: 'Home',
  data(){
  	return{
  		loading: true,
  		currency: null
  	}
  },
  async mounted(){
  	this.currency = await this.$store.dispatch('fetchCurrency')
  	console.log(this.currency)
  	this.loading = false
  	console.log(this.loading)
  },
  components: {
  	HomeBill,
		HomeCurrency
  },
  methods: {
  	async refresh(){
  		this.loading = true
  		this.currency = await this.$store.dispatch('fetchCurrency')
			this.loading = false
  	}
  }
}
</script>
