<template>
	<div class="col s12 m6 l8">
		<div class="card orange darken-3 bill-card">
			<div class="card-content white-text">
				<div class="card-header">
					<span class="card-title">{{'Exchange-rates' | localizeFilter}}</span>
				</div>
				<table>
					<thead>
						<tr>
							<th>{{'Exchange-rates' | localizeFilter}}</th>
							<th>Курс</th>
							<th>Дата</th>
						</tr>
					</thead>

					<tbody>
						<tr
							v-for="rate in currencies" :key="rate">
							<td>{{rate}}</td>
							<td>{{currency.rates[rate]}}</td>
							<td>{{currency.date}}</td>
						</tr>
					</tbody>
				</table>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: ['currency'],
	data(){
		return {
			currencies: ['RUB', 'USD', 'EUR'],
			// currencies: []
		}
	},
	mounted(){
		console.log(this.currency.rates)
	}
}
</script>