<template>
	<div class="col s12 m6 l4">
		<div class="card light-blue bill-card">
			<div class="card-content white-text">
				<span class="card-title">Счет в валюте</span>

				<p class="currency-line"
					v-for="item in currencies"
					:key="item">
					<span>{{getCurrency(item) | currencyFilter(item)}}</span>
				</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		props: ['rates'],
		data(){
			return{
				currencies: ['RUB', 'USD', 'EUR']
			}
		},
		mounted(){
			// console.log(currencyFilter(this.$store.getters.info.bill))
		},
		computed: {
			base(){
				return this.$store.getters.info.bill / (this.rates.RUB / this.rates.EUR)//получение количества денег у текущего (зарегистрированного) пользователя
				//преобразованное к рублям
			}
		},
		methods: {
			getCurrency(currency){
				// console.log(this.base )
				return Math.floor(this.base * this.rates[currency])
			}
		}
	}
</script>